/* @import-normalize; bring in normalize.css styles */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: var(--add-bg-color); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.loader {
  justify-content: center;
}

* {
  margin: 0;
  padding: 0;
}

html[data-theme='day'] {
  background: #fff;
  --plus-balance-color: #407946;
  --black-text-color: #000;
  --primary-text-color: #52555f;
  --white-color: #fff;
  --description-color: #c7ccdc;
  --bg-color: #f5f6fb;
  --table-color: #f5f6fb;
  --table-line: #f5f6fb;
  --add-bg-color: #fff;
  --input-bg-color: #fff;
  --active-link-bg: #fefefe;
  --not-active-link-bg: #fafbfd;
  --border-light: #f6f7fc;
  --text-with-opacity: rgba(82, 85, 95, 0.7);
  --box-shadow: none;
  --box-shadow-form: 0px 10px 60px rgba(170, 178, 197, 0.2);
  --back-transparent: #f5f6fb;
  --back-transparent-form: #ffffff;
  --text-shadow: none;
  --container-tab: #fff;
}

html[data-theme='night'] {
  --plus-balance-color: #06ff1f;
  --black-text-color: #fff;
  --primary-text-color: #fff;
  --white-color: rgb(6, 6, 31);
  --description-color: #f6f6f6;
  --bg-color: #3f5140;
  --table-color: #60646d;
  --add-bg-color: #4b4e55;
  --table-line: #fff;
  --input-bg-color: #60646d;
  --active-link-bg: #60646d;
  --not-active-link-bg: #4b4e55;
  --border-light: #fff;
  --text-with-opacity: #fff;
  --box-shadow: 0 0 10px #4aff1867, 0 0 10px #4aff1868, 0 0 40px #4aff1859,
    0 0 55px #4aff1866, 0 0 40px #4aff1865;
  --box-shadow-form: 0 0 10px #4aff1867, 0 0 10px #4aff1868, 0 0 40px #4aff1859,
    0 0 55px #4aff1866, 0 0 40px #4aff1865;
  --back-transparent: #f5f6fb46;
  --back-transparent-form: #ffffff42;
  --text-shadow: 0 0 15px #fff, 0 0 20px #4aff1867, 0 0 30px #4aff1868,
    0 0 40px #4aff1859, 0 0 55px #4aff1866, 0 0 75px #4aff1865;
  --container-tab: #00000096;
}
html[data-theme='night'] {
  background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
}

